(function() {
  var AssignedToSelectorController;

  AssignedToSelectorController = (function() {
    AssignedToSelectorController.$inject = [];

    function AssignedToSelectorController() {
      if (this.assigned) {
        this._getAssignedMember();
      }
      this._filterAssignedMember();
    }

    AssignedToSelectorController.prototype._getAssignedMember = function() {
      return this.assignedMember = _.filter(this.project.members, (function(_this) {
        return function(member) {
          return member.id === _this.assigned.get('id');
        };
      })(this));
    };

    AssignedToSelectorController.prototype._filterAssignedMember = function() {
      if (this.assigned) {
        return this.nonAssignedMembers = _.filter(this.project.members, (function(_this) {
          return function(member) {
            return member.id !== _this.assigned.get('id');
          };
        })(this));
      } else {
        return this.nonAssignedMembers = this.project.members;
      }
    };

    return AssignedToSelectorController;

  })();

  angular.module('taigaComponents').controller('AssignedToSelectorCtrl', AssignedToSelectorController);

}).call(this);


