(function() {
  var AssignedToController;

  AssignedToController = (function() {
    AssignedToController.$inject = ["tgLightboxFactory", "lightboxService"];

    function AssignedToController(lightboxFactory, lightboxService) {
      this.lightboxFactory = lightboxFactory;
      this.lightboxService = lightboxService;
      this.has_permissions = _.includes(this.project.my_permissions, 'modify_epic');
    }

    AssignedToController.prototype._closeAndRemoveAssigned = function() {
      this.lightboxService.closeAll();
      return this.onRemoveAssigned();
    };

    AssignedToController.prototype._closeAndAssign = function(member) {
      this.lightboxService.closeAll();
      return this.onAssignTo({
        'member': member
      });
    };

    AssignedToController.prototype.onSelectAssignedTo = function(assigned, project) {
      return this.lightboxFactory.create('tg-assigned-to-selector', {
        "class": "lightbox lightbox-assigned-to-selector open",
        "assigned": "assigned",
        "project": "project",
        "on-remove-assigned": "onRemoveAssigned()",
        "on-assign-to": "assignTo(member)"
      }, {
        "assigned": this.assignedTo,
        "project": this.project,
        "onRemoveAssigned": this._closeAndRemoveAssigned.bind(this),
        "assignTo": this._closeAndAssign.bind(this)
      });
    };

    return AssignedToController;

  })();

  angular.module('taigaComponents').controller('AssignedToCtrl', AssignedToController);

}).call(this);


